@media screen and (max-width: 1124px) {
  // header
  .hamburger-button {
    display: block; }
  .nav {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 100;
    width: 100%;
    flex-direction: column;
    align-items: center;
    padding: 0 15px;
    display: none;
    &__part {
      background: #666666;
      width: 100%;
      border-top: none;
      border-bottom: 1px solid #cecece;
      &:hover, &.active {
        border-top: none;
        border-bottom: 2px solid #ffc06f; } } }
  // biography
  .biography {
    &__container {
      flex-direction: column;
      align-items: center; } } }

@media screen and (max-width: 1000px) {
  .header {
    position: relative; }
  // gallery
  .gallery {
    &__album {
      width: 30%; } }
  .sliders {
    padding-top: 0; }
  .biography-page {
    &__hero {
      margin: 0; } }
  .contact {
    &__title {
      margin-top: 35px; } } }
@media screen and (max-width: 780px) {
  // header
  .header {
    &__container {
      align-items: center;
      height: 70px; }
    .logo {
      &__header {
        font-size: 30px;
        line-height: 30px; }
      &__footer-decor {
        width: 6px;
        height: 6px;
        background: #f6f5c5; }
      &__footer {
        align-items: center; }
      &__footer-text {
        font-size: 12px;
        color: #f6f5c5; } } }
  .biography-page-text {
    align-items: center; }
  .contact {
    &__name-contact {
      font-size: 30px;
      margin-bottom: 0; }
    &__email-contact {
      font-size: 18px; } }
  .title {
    display: flex;
    justify-content: center; }
  // slider
  .sliders {
    &__prev, &__next {
      display: none; } } }
@media screen and (max-width: 450px) {
  // header
  .logo {
    &__header {
      font-size: 24px;
      line-height: 24px; }
    &__footer {
      display: flex;
      align-items: center; }
    &__footer-text {
      font-size: 14px; }
    &__footer-decor {
      width: 4px;
      height: 4px; } }
  // biography
  .biography {
    &__photo {
      width: 100%; }
    &__person {
      width: 100%; } }
  .biography-text, .biography-page-text {
    &__title-header {
      font-size: 40px; }
    &__title-footer {
      font-size: 20px; }
    &__main {
      font-size: 16px;
      margin-bottom: 50px; }
    &__cap-letter {
      font-size: 31px;
      color: #f6f5c5;
      &-small {
        font-size: 16px; } } }
  .biography-page-text {
    &__main {
      line-height: 25px; } }
  // title
  .title {
    &__text {
      font-size: 25px;
      letter-spacing: 2px; }
    &__decor {
      width: 12px;
      height: 12px; } }
  // gallery
  .gallery {
    &__albums {
      flex-direction: column; }
    &__album {
      width: 100%;
      margin-bottom: 10px; } } }
